.wrapper {
  min-width: 350px;
  padding: 20px;
  .title {
    color: #161823;
    font-size: 3rem;
    font-weight: 600;
    text-align: center;
    margin-bottom: 30px;
  }
  input:-webkit-autofill,
  input:-webkit-autofill:hover,
  input:-webkit-autofill:focus,
  input:-webkit-autofill:active {
    -webkit-box-shadow: 0 0 0 30px white inset !important;
  }
  form {
    overflow: hidden;
    input {
      display: block;
      border: 1px solid rgba(22, 24, 35, 0.12);
      padding: 8px;
      width: 100%;
      &:first-child {
        margin-bottom: 15px;
      }
      &::placeholder {
        font-size: 1.6rem;
        color: #ccc;
      }
    }
  }
  .btnClose {
    position: absolute;
    top: 16px;
    right: 16px;
    background: rgba(22, 24, 35, 0.03);
    background: transparent;
    .icon {
      font-size: 3rem;
      color: rgb(170, 161, 161);
    }
  }
  h5 {
    color: #161823;
    font-size: 1.5rem;
    font-weight: 500;
    margin-top: 15px;
    span {
      margin-left: 5px;
      display: inline-block;
      color: #ff4c3a;
      font-weight: 600;
      cursor: pointer;
    }
  }
  .formGroup {
    margin-bottom: 15px;
    span {
      color: #fe2c55;
      display: block;
      margin-top: 10px;
      font-size: 1.6rem;
    }
  }
}
