.wrapper {
  width: 340px;
}

.item {
  padding: 8px 16px;
  display: block;
  cursor: pointer;
  &:hover {
    background-color: #f3f2f2;
  }
}

.title {
  font-size: 1.6rem;
  font-weight: 600;
}

.description {
  font-size: 1.2rem;
  font-weight: 300;
  opacity: 0.6;
}

.tickIcon {
  color: #20d5eb;
  font-size: 1.4rem;
  margin-left: 4px;
}

.viewAll {
  font-size: 1.4rem;
  padding: 16px 16px;

  &:hover {
    cursor: pointer;
    background-color: #f3f2f2;
  }
}
