.headerContainer {
  position: fixed;
  left: 0;
  top: 0;
  right: 0;
  height: 60px;
  background: #fff;
  border-bottom: 1px solid rgba(34, 90, 89, 0.2);
  z-index: 1000;
  .middle {
    display: flex;
    justify-content: space-around;
  }
  .headerContent {
    width: 100%;
    height: 100%;
    padding: 0;
    max-width: 1200px;
    margin: auto;
    padding-left: 20px;
    padding-right: 24px;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
  }
  .searchContainer {
    position: relative;
    max-width: 385px;
    .searchInput {
      display: flex;
      flex-direction: row;
      align-items: center;
      padding: 12px 16px;
      background: rgba(22, 24, 35, 0.06);
      border-radius: 92px;
      position: relative;
      overflow: hidden;
      z-index: 1;
      .inputText {
        font-weight: 400;
        font-size: 16px;
        line-height: 22px;
        border: none;
        background: transparent;
        outline: none;
        padding: 0;
        width: 292px;
        caret-color: #fe2c55;
        -webkit-appearance: textfield;
        -moz-appearance: textfield;
        appearance: textfield;
      }
      .resetInput .iconSpinner {
        animation: loading 2s linear infinite;
      }

      @keyframes loading {
        0% {
          transform: rotate(0deg);
        }
        100% {
          transform: rotate(360deg);
        }
      }
      .split {
        width: 1px;
        height: 28px;
        margin: -3px 0;
        background: rgba(22, 24, 35, 0.12);
      }
      .searchBtn {
        padding: 11px 16px 11px 12px;
        margin: -12px -16px;
        margin-left: 0;
        font-size: 0;
        background: transparent;
        .icon {
          width: 24px;
          height: 24px;
          color: rgba(22, 24, 35, 0.34);
        }
      }
      .border {
        display: none;
        position: absolute;
        left: 0;
        top: 0;
        right: 0;
        bottom: 0;
        border: 1.5px solid rgba(22, 24, 35, 0.2);
        border-radius: 92px;
        z-index: -1;
      }
      .icon {
        margin: 0 12px;
        font-size: 1.6rem;
        color: rgba(22, 24, 35, 0.34);
      }
    }
  }
  .menuRight {
    display: flex;
    flex-direction: row;
    align-items: center;
    .avatar {
      img {
        width: 32px;
        height: 32px;
        border-radius: 50%;
      }
    }
    .iconUser {
      margin-right: 16px;
      cursor: pointer;
      .icon {
        font-size: 2.3rem;
      }
    }
    .iconWrap {
      position: relative;
      cursor: pointer;
      margin-left: 16px;
      .icon {
        padding: 0 4px;
        display: block;
        width: 28px;
        height: 28px;
        color: #000;
      }
    }
  }
  .textVi {
    display: flex;
    align-items: center;
    .icon {
      font-size: 1.6rem;
      margin-right: 6px;
    }
  }
  .avatar {
    width: 32px;
    height: 32px;
    border-radius: 50%;
    margin-right: 16px;
    cursor: pointer;
  }
}

@media screen and (max-width: 500px) {
  .headerContainer .searchContainer .searchInput {
    display: none;
  }
}
